.theme-switch__toggle + label {
  width: 42px;
  height: 20px;
  cursor: pointer;
  background: linear-gradient(to right, #ff6b08 0, #fc8b34 45%, #545454 55% 100%) 0 0 / 134px 100%;
  border-radius: 70px;
  padding: 1px;
  transition: all .5s;
  display: inline-block;
  position: relative;
  box-shadow: inset 0 0 10px #0003;
}

@media screen and (min-width: 768px) {
  .theme-switch__toggle + label {
    width: 64px;
    height: 32px;
    background-size: 178px 100%;
    padding: 2px;
  }
}

.theme-switch__toggle + label:before {
  content: "";
  width: 16px;
  height: 16px;
  text-align: center;
  color: #ff6b08;
  text-transform: uppercase;
  background: wheat;
  border-radius: 100%;
  font-family: Cabin, sans-serif;
  font-size: 8px;
  font-weight: bold;
  line-height: 16px;
  transition: left .5s, color .5s, transform .15s;
  position: absolute;
  left: 1px;
  right: 1px;
}

@media screen and (min-width: 768px) {
  .theme-switch__toggle + label:before {
    width: 26px;
    height: 26px;
    font-size: 14px;
    line-height: 28px;
    left: 2px;
    right: 2px;
  }
}

.theme-switch__toggle + label:active .theme-switch__toggle + label:before {
  transform: scale(.95);
}

.theme-switch__toggle:checked + label {
  background-position-x: -92px;
}

@media screen and (min-width: 768px) {
  .theme-switch__toggle:checked + label {
    background-position-x: -114px;
  }
}

.theme-switch__toggle:checked + label:before {
  content: "";
  color: #007177;
  left: 25px;
}

@media screen and (min-width: 768px) {
  .theme-switch__toggle:checked + label:before {
    left: 36px;
  }
}

